/* Header.css */

#navigation-bar {
  display: flex;
  justify-content: center;
  align-items: center;
  padding-top: 30px;
}

.App-logo {
  width: 200px;
  height: auto;
}

#nav-wrap {
  margin-top: 20px;
  display: flex;
  justify-content: center;
}

#main-nav {
  display: flex;
  justify-content: center;
}

.nav-list {
  list-style: none;
  display: flex;
  gap: 40px;
  padding: 0;
  margin: 0;
}

.nav-list li {
  position: relative;
  font-weight: bold;
  font-size: 1rem;
}

.dropdown-toggle {
  cursor: pointer;
  text-transform: lowercase;
}

.dropdown-menu {
  display: none;
  position: absolute;
  top: 100%;
  background-color: #fff;
  padding: 10px 0;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
  border-radius: 6px;
  z-index: 1000;
  min-width: 160px;
}

.dropdown-menu li {
  padding: 8px 20px;
  white-space: nowrap;
}

.dropdown:hover .dropdown-menu {
  display: block;
}

.dropdown-menu a {
  text-decoration: none;
  color: #000;
}

.dropdown-menu a:hover {
  background-color: #f0f0f0;
  display: block;
}
