/* ========== Fonts ========== */
@font-face {
  font-family: "Poppins";
  font-style: normal;
  font-weight: 300;
  src: url("./assets/fonts/poppins/poppins-v23-latin-300.woff2") format("woff2");
}

@font-face {
  font-family: "Poppins";
  font-style: normal;
  font-weight: 400;
  src: url("./assets/fonts/poppins/poppins-v23-latin-regular.woff2") format("woff2");
}

@font-face {
  font-family: "Poppins";
  font-style: normal;
  font-weight: 500;
  src: url("./assets/fonts/poppins/poppins-v23-latin-500.woff2") format("woff2");
}

@font-face {
  font-family: "Poppins";
  font-style: normal;
  font-weight: 600;
  src: url("./assets/fonts/poppins/poppins-v23-latin-600.woff2") format("woff2");
}

@font-face {
  font-family: "Poppins";
  font-style: normal;
  font-weight: 700;
  src: url("./assets/fonts/poppins/poppins-v23-latin-700.woff2") format("woff2");
}

/* ========== Layout ========== */
html, body {
  margin: 0;
  font-family: "Poppins", sans-serif;
  color: #2c2d31;
  background-color: #CAE5DC;
}

html, body, #root {
  margin: 0;
}

.page-container {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

.content {
  flex: 1;
}

/* ========== App Structure ========== */
.App {
  display: flex;
  flex-direction: column;
  text-align: center;
}

.App-logo {
  height: 20vmin;
  pointer-events: none;
}

.App-header {
  background-color: #CAE5DC;
  padding: 2rem 0;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.App-link {
  color: #61dafb;
}

/* ========== Navigation ========== */
#navigation-bar {
  background-color: #CAE5DC;
  width: 100%;
  height: auto;
}

#nav-wrap {
  margin-top: 10px;
}

.nav-list {
  display: flex;
  list-style: none;
  gap: 4rem;
  justify-content: center;
  padding: 0;
  margin: 0;
  background-color: #CAE5DC;
  font-size: 20px;
  position: relative;
}

.nav-list li {
  position: relative;
  list-style: none;
}

.dropdown-toggle {
  cursor: pointer;
  font-weight: 600;
  color: #111;
  font-size: 16px;
}

/* ========== Dropdown Menu ========== */
.dropdown-menu {
  display: none;
  position: absolute;
  top: 100%;
  left: 0;
  background-color: #ffffff;
  padding: 0.5rem 0;
  min-width: 180px;
  box-shadow: 0 4px 8px rgba(0,0,0,0.1);
  z-index: 99;
  border-radius: 6px;
  list-style: none;
  margin: 0;
  font-size: 14px;
  font-weight: 500;
}

.dropdown-menu li {
  padding: 0;
  margin: 0;
}

.dropdown-menu li a {
  display: block;
  padding: 0.5rem 1rem;
  color: #2C2D31;
  text-decoration: none;
  font-size: 14px;
  font-weight: 500;
}

.dropdown-menu li a:hover {
  color: #50796A;
  background-color: #F0F7F4;
}

.dropdown:hover .dropdown-menu {
  display: block;
}

/* ========== Footer ========== */
#copyright {
  align-items: center;
  justify-content: center;
}

.social-icons {
  display: flex;
  justify-content: center;
  gap: 1.5rem;
  margin-top: 1rem;
}

.social-icons a {
  font-size: 24px;
  color: #000000; /* black by default */
  transition: color 0.3s ease;
}

.social-icons a:hover {
  color: #91c8b0; /* sage green on hover */
}

.social-icons a .icon-circle {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  background-color: transparent;
  border: 2px solid #000;
  border-radius: 50%;
  font-weight: 700;
  font-family: "Poppins", sans-serif;
  font-size: 16px;
  width: 32px;
  height: 32px;
  color: #000;
  transition: all 0.3s ease;
}

.social-icons a:hover .icon-circle {
  border-color: #91c8b0;
  color: #91c8b0;
}

.social-icon {
  width: 32px;
  height: 34px;
  filter: brightness(0);
  transition: filter 0.3s ease;
  margin: 0 10px;
}

.social-icon:hover {
  filter: brightness(0) saturate(100%) sepia(20%) hue-rotate(100deg);
}

#Main-body {
  padding: 2rem;
}

/* Main two-column layout */
.main-content {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 2rem;
  max-width: 1200px;
  margin: 0 auto 1rem auto;
}

.main-left,
.main-right {
  flex: 1 1 300px;
  padding: 1rem;
  background-color: #ffffff;
  border-radius: 8px;
  box-shadow: 0 2px 6px rgba(0, 0, 0, 0.1);
}

/* Small third row aligned right */
.main-footer-right {
  max-width: 1200px;
  margin: 0 auto;
  display: flex;
  justify-content: flex-end;
  font-size: 12px;
  color: #2c2d31;
}

.main-footer-right a {
  color: #50796A;
  text-decoration: none;
  font-weight: 500;
  margin-left: 0.75rem;
}

.main-footer-right a:hover {
  text-decoration: underline;
}

@media (max-width: 768px) {
  .main-content {
    flex-direction: column;
    align-items: center;
  }

  .main-footer-right {
    justify-content: center;
    text-align: center;
    font-weight: bold;
  }
}

#bmc-qr {
  width: 200px;
  height: 200px;
}


.hero-banner {
  background-color: #b9d8c3;
  padding: 3rem 1rem;
  text-align: center;
  border-radius: 12px;
  margin-bottom: 2rem;
}
.cta-button {
  display: inline-block;
  margin-top: 1rem;
  padding: 0.7rem 1.5rem;
  background: black;
  color: white;
  border-radius: 25px;
  text-decoration: none;
  font-weight: 600;
}

