/* src/components/footer/Footer.css */

#footer-div {
  background-color: #f5f5f5;
  padding: 20px 0;
  text-align: center;
  margin-top: 60px;
}

.social-icons {
  display: flex;
  justify-content: center;
  gap: 20px;
  margin-bottom: 15px;
}

.social-icon,
.social-icons svg {
  width: 24px;
  height: 24px;
  color: #333;
  transition: color 0.2s ease;
}

.social-icons a:hover svg,
.social-icons a:hover .social-icon {
  color: #0968f6;
  filter: brightness(1.2);
}
