.content-row {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 2rem;
  max-width: 1200px;
  margin: 1rem auto;
  padding: 1rem;
}

.content-left,
.content-right,
.content-single {
  flex: 1 1 100%;
  padding: 1rem;
  background-color: #ffffff;
  border-radius: 8px;
  box-shadow: 0 2px 6px rgba(0, 0, 0, 0.1);
}

.content-left,
.content-right {
  flex: 1 1 300px;
}
